<template>
  <div class="pa-4">
    <h1>Vue数据绑定</h1>
    <div>
      <!-- eslint-disable -->
      <input
        placeholder="输入num" 
        v-bind="{ value: num }"
        type="number"
        style="width: 50%;border: 1px solid lightblue;"
        v-on:input="num = Number($event.target.value)"
      >
      <span>{{ num }}</span>
      <v-btn @click="num += 1">
        num + 1
      </v-btn>
    </div>
    <p>无法监听到的变化</p>
    <hr class="my-2">
    <p>无法劫持新创建的属性，为了解决这个问题，Vue 提供了 Vue.set 以创建新属性</p>
    obj:{{ obj }}
    <v-btn @click="addNewProp"><code>this.obj["2"] = 2;</code></v-btn>
    <v-btn @click="addNewPropSet"><code>this.$set(this.obj, "3", 3);</code></v-btn>
    <v-btn @click="resetProp"><code>this.obj = { "1" : 1 };</code></v-btn>
    <hr class="my-2">
    <p>无法劫持数组的变化，为了解决这个问题，Vue 对数组原生方法进行了劫持</p>
    arr:{{ arr }}<br>
    <v-btn @click="arr.push(4)"><code>this.arr.push(4);</code></v-btn>
    <v-btn @click="arr.pop()"><code>this.arr.pop();</code></v-btn>
    <v-btn @click="arr.shift()"><code>this.arr.shift();</code></v-btn>
    <v-btn @click="arr.unshift(0)"><code>this.arr.unshift(0);</code></v-btn>
    <v-btn @click="arr.splice(1, 1)"><code>this.arr.splice(1, 1);</code></v-btn>
    <v-btn @click="arr.reverse()"><code>this.arr.reverse();</code></v-btn>
    <v-btn @click="arr.sort()"><code>this.arr.sort();</code></v-btn>
    <p>无法劫持利用索引修改数组元素，这个问题同样可以用 Vue.set 解决。</p>
    <v-btn @click="arr[1] = 2"><code>this.arr[1] = 2;</code></v-btn>
    <v-btn @click="$set(arr, 1, 2)"><code>this.$set(this.arr, 1, 2);</code></v-btn>
    <hr class="my-2">
    <p>Vue3使用Proxy，所以没有这些问题</p>
  </div>
</template>

<script>

export default {
  data () {
    return {
      num: 0,
      obj: { "1" : 1 },
      arr: [1, 2, 3],
    }
  },

  methods: {
    addNewProp() {
      this.obj["2"] = 2;
      console.log(this.obj);
    },
    addNewPropSet() {
      this.$set(this.obj, "3", 3);
      console.log(this.obj);
    },
    resetProp() {
      this.obj = { "1" : 1 };
      console.log(this.obj);
    },
  },
}
</script>

